import React, { Component } from "react";
import { connect } from "react-redux";

import Header from "../../containers/Header/index";
import Footer from "../../containers/Footer/index";
import LazyImage from "../../components/LazyImage/index";
import "./styles.css";

import HeaderAbout from "../../containers/HeaderAbout/index";
import SectionAboutUs from "../../containers/SectionAboutUs/index";
import WhatWeHave from "../../components/WhatWeHave/index";
import MainLoadBar from "../../components/MainLoadBar/index"

class About extends Component {
    constructor(){
        super();
        this.state = {
            loaded: false
        }
    }
    componentDidMount(){
        const img = new Image();
        img.onload = () => {
            setTimeout(()=>{this.setState({ loaded: true })},1000);
        };
        img.src = "background.jpg";

    }
  render() {
        if(!this.state.loaded) {
            return (
                <div className="about fix">
                    <Header />
                        <MainLoadBar/>
                    <Footer  />
                </div>
            )
        }
        else{
            return (
                <div className="about">
                    <Header />
                    <HeaderAbout />
                    <SectionAboutUs />
                    <WhatWeHave />
                    <Footer   />
                </div>
            );
        }

  }
}

export default connect(state => ({
  firstColumnText: state.about.columns.first,
  secondColumnText: state.about.columns.second,
  thirdColumnText: state.about.columns.third,
    ammountImages: state.mainPageLoader.images
}))(About);



