import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";

import Header from "../../containers/Header/index";
import Footer from "../../containers/Footer/index";
import LazyImage from "../../components/LazyImage/index";
import MainLoadBar from "../../components/MainLoadBar/index"
import images from "../../images";
import "./styles.css";

import BasketContent from "../../containers/BasketContent";

class Basket extends Component {
    constructor(){
        super();
        this.state = {
            loaded: false
        }
    }
    componentDidMount(){

            setTimeout(()=>{this.setState({ loaded: true })},1500);

    }
  render() {
    if (this.props.quantity !== 0) {
        if(this.state.loaded){
            return (
                <div className="basket">
                    <Header />

                    <BasketContent />

                    <Footer />
                </div>
            );
        }else{
            return(

                <div className="basket fix">
                    <Header />

                    <MainLoadBar/>

                    <Footer />
                </div>

                )
        }

    } else {
      return <Redirect to={"/kdd_react/"} />;
    }
  }
}

export default connect(state => ({
  quantity: state.sidebar.basket.result.quantity
}))(Basket);



