import React, { Component } from "react";
import { connect } from "react-redux";

import line from "../../../img/line.png"
import round from "../../../img/round.png"

import "./styles.css";


class MainLoadBar extends Component {


    render() {

        return(

            <div className={"loadBar"}>
                <div className={'box_wrapper'}>
                    <div className={"back_box"}><img src={line}/></div>
                    <div className={"middle_box"}></div>
                    <div className={"front_box"}><img src={round} alt=""/></div>
                </div>
            </div>

            )
    }
}

export default connect(state => ({
    firstColumnText: state.about.columns.first,
    secondColumnText: state.about.columns.second,
    thirdColumnText: state.about.columns.third,
    ammountImages: state.mainPageLoader.images
}))(MainLoadBar);



