import React, { Component } from "react";
import { connect } from "react-redux";

import Header from "../../containers/Header/index";
import Footer from "../../containers/Footer/index";
import TradeMarksHeader from "../../components/TradeMarksHeader";
import TradeMarksMain from "../../components/TradeMarksMain";
import MainLoadBar from "../../components/MainLoadBar/index"
import "./styles.css";

class TradeMarks extends Component {
    constructor(){
        super();
        this.state = {
            loaded: false
        }
    }
    componentDidMount(){
        const img = new Image();
        img.onload = () => {
            setTimeout(()=>{this.setState({ loaded: true })},1000);
        };
        img.src = "trade_marks_header.jpg";

    }
  render() {
        if(this.state.loaded){
            return (
                <div className="trade_marks">
                    <Header />
                    <TradeMarksHeader />
                    <TradeMarksMain />
                    <Footer />
                </div>
            );
        }else{
            return(
                <div className="trade_marks fix">
                    <Header />
                        <MainLoadBar/>
                    <Footer />
                </div>

            )
        }

  }
}

export default connect(state => ({}))(TradeMarks);



